<template>
  <v-container>
    <v-row>
      <v-col cols="12" sm="6" md="4">
        <span class="body-1 font-weight-medium"> Pays enregistrés </span>
        <v-chip v-if="countries?.length" color="purple lighten-1 white--text">
          {{ countries?.length }}
        </v-chip>
        <v-text-field
          color="red"
          v-model="teamFilter"
          label="Filtrer par nom de pays"
        />
      </v-col>
    </v-row>
    <v-row v-if="loading == true">
      <v-progress-linear indeterminate></v-progress-linear>
    </v-row>
    <v-row>
      <v-col
        v-for="(country, i) in filteredCountries"
        :key="i"
        cols="12"
        sm="6"
        md="4"
      >
        <v-card>
          <v-card-title justify="center">
            <v-chip label outlined>
              {{ country.team }}
            </v-chip>
          </v-card-title>
          <v-card-text>
            <p>
              <v-icon>mdi-soccer-field</v-icon> Matchs joués:
              <v-chip>{{
                country.gamesAwayTeamAggregate.count +
                country.gamesHomeTeamAggregate.count
              }}</v-chip>
            </p>
            <p>
              <v-icon color="purple darken-1">mdi-home</v-icon>
              Domicile:
              {{ country.gamesHomeTeamAggregate.count }}
            </p>
            <p>
              <v-icon color="light-blue darken-1">mdi-airplane</v-icon>
              Exterieur:
              {{ country.gamesAwayTeamAggregate.count }}
            </p>
            <p>
              <v-icon color="red darken-1">mdi-map-marker</v-icon>
              Nombre de fois organisateur :
              {{ country.isHostHostsAggregate.count }}
            </p>
            <p>
              <v-icon color="yellow darken-1">mdi-star</v-icon>
              {{ getNoCup(country?.team) }}
            </p>
          </v-card-text>
        </v-card>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
import {
  GET_ALL_COUNTRIES_WITH_CUP_STATS,
  GET_ALL_CHAMPIONS,
} from "@/graphql/queries";

export default {
  name: "PaysView",
  data: () => ({
    yearFilter: "",
    teamFilter: "",
    countries: [],
    noCupPerCountry: [],
    allChampions: [],
    colors: [],
    loading: false,
  }),
  methods: {
    async getCountries() {
      this.loading = true;
      await this.$apollo
        .query({
          query: GET_ALL_COUNTRIES_WITH_CUP_STATS,
        })
        .then((result) => {
          this.countries = result.data.countries;
          this.loading = false;
        });
    },
    async getChampions() {
      await this.$apollo
        .query({
          query: GET_ALL_CHAMPIONS,
        })
        .then((result) => {
          this.allChampions = result.data?.cups;
        });
    },
    getNoCup(country) {
      const cupCount = this.allChampions.filter((b) =>
        b.champion.includes(country)
      ).length;
      return cupCount;
    },
    getColor() {
      return (
        "#" +
        Math.floor(Math.random() * 16777215)
          .toString(16)
          .padStart(6, "0")
          .toUpperCase()
      );
    },
    generateColors() {
      for (let index = 0; index < this.countries.length; index++) {
        this.colors.push(this.getColor());
      }
    },
  },
  computed: {
    filteredCountries() {
      return this.countries?.filter((country) => {
        return country.team
          .toString()
          .toLowerCase()
          .includes(this.teamFilter.toLowerCase());
      });
    },
  },
  created() {
    this.$watch(
      "$apollo.loading",
      (loading) => {
        this.loading = loading;
      },
      { immediate: true }
    );
  },
  mounted() {
    this.getChampions();
    this.getCountries().then(() => this.generateColors());
  },
};
</script>
